.sendForm {
    display: flex;
    justify-content: center;
    gap: 44px;
    margin-top: 56px;
    /*position: relative;*/
}

.textarea {
    font-family: "Montserrat", sans-serif;
    font-size: 14px;
    color: rgba(0, 0, 0, 0.6);
    opacity: 0.9;

    border-radius: 20px;
    max-width: 976px;
    width: 100%;
    min-height: 40px;
    box-shadow: 0 1px 2px 0 rgba(29, 33, 38, 0.1), 0 5px 20px 0 rgba(29, 33, 38, 0.03);
    background: #f5f7fb;
    padding: 10px 24px;
    resize: none;
    max-height: 150px;
}

.button {
    font-weight: 600;
    font-size: 16px;
    color: #fff;
    border-radius: 20px;
    max-width: 120px;
    width: 100%;
    height: 40px;
    background-color: #0066CC;
    border: none;
    cursor: pointer;
    transition: 0.2s;
}

.button:hover {
    background-color: #0a4f96;
    scale: 1.02;
}

.button:active {

}

.error {
    color: red;
    /*position: relative;*/
    top: 100%;
    left: 25px;
    margin-top: 5px;
    pointer-events: none;
}